html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: "Arial";
    font-size: 1.6rem;
    line-height: 1.8;
    font-family: 'Cormorant Garamond';
    @include tablet {
        background-color: $grisOscuro;
    }
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

.contenedorMayor {
    width: 98%;
    max-width: 140rem;
    margin: 0 auto;
}

h1, h2, h3 {
    margin: 0;
    font-weight: 300;
    text-align: center;
    color: $negro;
}
ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}
p {
    font-size: 2rem;
    color: $negro;
}

a {
    text-decoration: none;
    color: $negro;
}
img,
picture {
    width: 99%;
    display: block;
    object-fit: cover;
}
